import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import { Navigation, Pagination } from "swiper";

const Banner = () => {
  const banners = [
    { id: 1, image: "/banner1.jpg", text: "Birinchi banner" },
    { id: 2, image: "/banner2.jpg", text: "Ikkinchi banner" },
    { id: 3, image: "/banner3.jpg", text: "Uchinchi banner" },
  ];

  return (
    <Swiper
      modules={[Navigation, Pagination]}
      navigation
      pagination={{ clickable: true }}
      loop
      className="h-64"
    >
      {banners.map((banner) => (
        <SwiperSlide key={banner.id}>
          <div
            className="h-full bg-cover bg-center flex items-center justify-center text-white"
            style={{ backgroundImage: `url(${banner.image})` }}
          >
            <h2 className="text-2xl font-bold bg-black bg-opacity-50 p-4 rounded">
              {banner.text}
            </h2>
          </div>
        </SwiperSlide>
      ))}
    </Swiper>
  );
};

export default Banner;
